import { $axios } from "./app-http";

export async function registerPatient(payload) {
    const { data } = await $axios.post(
        "patient-msg-log/register-patient",
        payload
    );
    return data;
}

export async function getIsPatientRegistered(id) {
    const { data } = await $axios.get(
        `patient-msg-log/is-patient-registered/${id}`
    );
    return data;
}
