<template>
    <div class="p-fluid p-formgrid p-grid p-input-filled p-jc-between">
        <div class="p-field p-col-12 p-md-4">
            <label for="patient-search">Patient Search</label>
            <div class="p-inputgroup">
                <p-auto-complete
                    v-model="selectedPaitent"
                    :suggestions="filteredPatients"
                    @complete="searchPateent($event)"
                    field="name"
                />
                <span class="p-inputgroup-addon">
                    <i class="pi pi-search"></i>
                </span>
            </div>
        </div>
        <div class="p-field p-col-12 p-md-1" v-if="isRegistered">
            <label>-</label>
            <br />
            <p-button label="Reset Password" />
        </div>
        <div class="p-field p-col-12 p-md-2" v-else>
            <label>Click to register the patient for messaging</label>
            <br />
            <p-button
                label="Register"
                style="width: 100px"
                @click="showRegisterDialog"
            />
        </div>
    </div>

    <div class="p-fluid p-formgrid p-grid p-input-filled">
        <div class="p-field p-col-12 p-md-4">
            <label>Subject</label>
            <p-input-text />
        </div>
        <div class="p-col-12 p-md-8"></div>
        <div class="p-field p-col-12 p-md-6">
            <label>Message</label>
            <p-textarea rows="5" />
        </div>
    </div>
    <p-button label="send" class="p-mr-2" />
    <p-button label="Print Message History" />
</template>

<script>
import { computed, inject, ref } from "vue";
export default {
    setup() {
        const registerDialog = inject("registerDialog");
        const isRegistered = inject("isRegistered");

        const patients = ref([{ name: "Aberdine" }]);
        const currentPatient = ref("Thamlingam Tarhami");
        const selectedPaitent = inject("selectedPaitent");
        const filteredPatients = ref([]);
        const messageHistories = ref([
            { date: "2021-01-10", message: "weocome to newlife" }
        ]);

        const searchPateent = (event) => {
            if (!event.query.trim().length) {
                filteredPatients.value = [...patients.value];
            } else {
                filteredPatients.value = patients.value.filter((patient) => {
                    return patient.name
                        .toLowerCase()
                        .startsWith(event.query.toLowerCase());
                });
            }
        };

        return {
            isRegistered,
            currentPatient,
            selectedPaitent,
            filteredPatients,
            searchPateent,
            messageHistories,
            showRegisterDialog: () => (registerDialog.value = true)
        };
    }
};
</script>
